<template>
  <div class="common-table">
    <!--  -->
    <div v-if="showSearchBar" class="search-bar">
      <div class="left-block" v-if="showSearchBlock">
        <el-select
          v-model="searchKey"
          placeholder=""
          class="search-key"
          :size="controlSize"
          filterable
          clearable
        >
          <el-option
            v-for="(item, index) in searchFields"
            :label="getColumnLabel(item)"
            :value="item"
            :key="'search-field-' + index"
          />
        </el-select>
        <el-input
          v-model="searchWord"
          placeholder=""
          clearable
          type="text"
          class="search-input"
          :size="controlSize"
        />
        <el-button
          type="primary"
          :size="controlSize"
          class="search-btn"
          icon="el-icon-search"
          @click="onSearch"
        ></el-button>
        <el-button
          v-if="showMoreBtn"
          :icon="showMore?'el-icon-s-unfold':'el-icon-s-fold'"
          type="info"
          class="show-more-btn"
          :size="controlSize"
          @click="showMore = !showMore"
        ></el-button>
      </div>
      <slot name="search-middle" />
      <div class="right-block">
        <el-select
          v-if="showHide"
          :size="controlSize"
          v-model="hiddenTableColumn"
          placeholder=""
          class="hidden-select"
          multiple
          collapse-tags
          filterable
        >
          <el-option
            v-for="(item, index) in defaultTableColumn"
            :key="'hidden-column-index-' + index"
            :label="getColumnLabel(item)"
            :value="item"
          />
        </el-select>
        <el-dropdown
          v-if="showHide"
          :size="controlSize"
          class="hidden-reset"
          @command="onResetCommand"
        >
          <el-button
            type="primary"
            :size="controlSize"
            class="hidden-reset-btn"
            icon="el-icon-arrow-down"
          ></el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="filter" icon="el-icon-arrow-down"></el-dropdown-item>
            <el-dropdown-item command="search" divided icon="el-icon-search"></el-dropdown-item>
            <el-dropdown-item command="sort" divided icon="el-icon-sort"></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-button
          v-if="showAdd"
          type="success"
          :size="controlSize"
          class="add-btn"
          icon="el-icon-circle-plus-outline"
          @click="$emit('onAdd')"
        ></el-button>
      </div>
    </div>
    <el-drawer :visible.sync="showMore" title="" v-bind="getMoreBlockProp()">
      <slot name="more-options"></slot>
    </el-drawer>
    <!--  -->
    <el-table
      ref="dataTable"
      :data="tableData"
      v-bind="getTableProp()"
      @selection-change="onSelectionChange"
      @filter-change="onFilterChange"
      @sort-change="onSortChange"
    >
      <template v-for="(val, key) in tableColumn">
        <!-- icon -->
        <el-table-column
          :key="'table-column-' + key"
          v-if="val.type === 'drag'"
          align="center"
          width="40"
        >
          <template>
            <i class="el-icon-rank" />
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="!val.hidden && val.render"
          :type="val.type"
          :column-key="val.field"
          :label="columnName(val)"
          :prop="val.field"
          :formatter="tableFormatter"
          :filters="tableFilters && tableFilters(val.field)"
          :filtered-value="filterValue(val.field)"
          :filter-multiple="tableFilterMultiple && tableFilterMultiple[val.field]"
          :sortable="val.sortable"
          :width="val.width"
          :key="'table-column-' + key"
          :fixed="val.fixed"
        >
          <template slot-scope="{ row, column, $index }">
            <slot
              :row="row"
              :column="column"
              :colKey="column.columnKey"
              :val="row[column.columnKey]"
              :index="$index"
              name="table-custom"
            />
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="!val.hidden && !val.render"
          :type="val.type"
          :fixed="!!val.fixed"
          :column-key="val.field"
          :prop="val.field"
          :label="columnName(val)"
          :formatter="tableFormatter"
          :filters="tableFilters && tableFilters(val.field)"
          :filtered-value="filterValue(val.field)"
          :filter-multiple="tableFilterMultiple && tableFilterMultiple[val.field]"
          :sortable="val.sortable"
          :width="val.width"
          :key="'table-column-' + key"
        />
      </template>
    </el-table>
    <!--  -->
    <div v-if="showPagination" class="pagination-container">
      <div class="flex-main flex-row">
        <el-button v-if="showMutilDel" :size="controlSize" type="danger" @click="onMutilDelete"></el-button>
        <slot name="bottom" />
      </div>
      <el-pagination
        :current-page="pagination.page"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pagination.limit"
        :total="pagination.total"
        background
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="onSizeChange"
        @current-change="onCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import Sortable from 'sortablejs'
import { calShortcuts, isEmptyObj, error } from '@/utils'
import moment from 'moment'

export default {
  name: 'ComTable',
  props: {
    // 
    controlSize: {
      type: String,
      default: 'medium'
    },

    /********  *********/
    // 
    showSearchBar: {
      type: Boolean,
      default: false
    },
    // 
    showSearchBlock: {
      type: Boolean,
      default: true
    },
    // 
    searchFields: {
      type: Array,
      default: () => []
    },
    // 
    searchParams: {
      type: Object,
      default: () => {
        return {
          key: '',
          word: ''
        }
      }
    },
    // 
    showMoreBtn: {
      type: Boolean,
      default: false
    },
    // drawer
    moreBlockProp: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 
    showHide: {
      type: Boolean,
      default: true
    },
    // 
    showAdd: {
      type: Boolean,
      default: false
    },

    /********  *********/
    // 
    tableData: {
      type: Array,
      default: () => []
    },
    // 
    tableColumn: {
      type: Array,
      default: () => []
    },
    // 
    filterParams: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 
    sortParams: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 
    tableFormatter: {
      type: Function,
      default: undefined
    },
    // 
    tableFilters: {
      type: Function,
      default: undefined
    },
    // 
    tableFilterMultiple: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 
    selected: {
      type: Array,
      default: () => []
    },
    // table
    tableProp: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 
    draggable: {
      type: Boolean,
      default: false
    },

    /********  *********/
    // 
    showPagination: {
      type: Boolean,
      default: false
    },
    // 
    pagination: {
      type: Object,
      default: () => {
        return {}
      }
    },
    // 
    showMutilDel: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pickerOptions: {
        shortcuts: calShortcuts
      },
      draggableElement: null,
      // 
      searchKey: '',
      searchWord: '',
      // 
      showMore: false
    }
  },
  computed: {
    // 
    defaultTableColumn() {
      return this.tableColumn.reduce((list, cur) => {
        if (cur.field) {
          list.push(cur.field)
        }
        return list
      }, [])
    },
    // tableColumnfiledmap
    columnMap() {
      const map = {}
      this.tableColumn.forEach(item => {
        map[item.field] = item
      })
      return map
    },
    // 
    hiddenTableColumn: {
      get: function() {
        return this.tableColumn
          .filter(item => item.hidden && item.field !== 'operation')
          .map(item => item.field)
      },
      set: function(newVal) {
        this.tableColumn.forEach(item => {
          this.$set(item, 'hidden', newVal.includes(item.field))
        })
      }
    }
  },
  watch: {
    async tableData(newVal) {
      if (newVal.length > 0) {
        await this.$nextTick()
        if (this.draggable) {
          this.setDraggable()
        }
      }
    },
    // 
    searchKey(value) {
      this.$emit('update:searchParams', {
        key: value,
        word: this.searchWord
      })
    },
    searchWord(value) {
      this.$emit('update:searchParams', {
        key: this.searchKey,
        word: value
      })
    }
  },
  async created() {},
  methods: {
    getTableProp() {
      return {
        fit: true,
        border: true,
        ...this.tableProp
      }
    },
    getMoreBlockProp() {
      return {
        direction: 'rtl',
        size: '400px',
        ...this.moreBlockProp
      }
    },
    getColumnLabel(field) {
      const item = this.columnMap[field]
      return item ? item.label : ''
    },
    columnName(val) {
      if (val.label) return val.label
      return val.field
    },
    filterValue(filed) {
      if (filed in this.filterParams) {
        return this.filterParams[filed]
      } else {
        return []
      }
    },
    // 
    onSizeChange(val) {
      this.$emit('update:pagination', { ...this.pagination, listRows: val })
      this.$emit('getList')
    },
    // 
    onCurrentChange(val) {
      this.$emit('update:pagination', { ...this.pagination, page: val })
      this.$emit('getList')
    },
    // getList
    onSearch() {
      this.$emit('update:pagination', { ...this.pagination, page: 1 })
      this.$emit('getList')
    },
    // 
    onSelectionChange(selected) {
      this.$emit('update:selected', selected)
    },
    // 
    onFilterChange(filter) {
      this.$emit('update:filterParams', { ...this.filterParams, ...filter })
      this.$emit('update:pagination', { ...this.pagination, page: 1 })
      this.$emit('getList')
    },
    // 
    onSortChange(sort) {
      if (!sort.prop) {
        // 
        this.$emit('update:sortParams', {})
      } else {
        // 
        this.$emit('update:sortParams', {
          [`sort[${sort.prop}]`]: sort.order === 'ascending' ? 'asc' : 'desc'
        })
      }
      // 
      this.$emit('update:pagination', { ...this.pagination, page: 1 })
      this.$emit('getList')
    },
    // 
    onResetCommand(command) {
      switch (command) {
        case 'search':
          // 
          if (this.searchParams.key === '' && this.searchParams.word === '')
            return
          this.searchKey = ''
          this.searchWord = ''
          break
        case 'filter':
          // 
          if (isEmptyObj(this.sortParams)) return
          this.$emit('update:filterParams', {})
          this.$refs.dataTable.clearFilter()
          break
        case 'sort':
          // 
          if (isEmptyObj(this.sortParams)) return
          this.$emit('update:sortParams', {})
          this.$refs.dataTable.clearSort()
          break
        default:
          break
      }

      // 
      this.$refs.dataTable.clearSelection()
      this.$emit('update:pagination', { ...this.pagination, page: 1 })
      this.$emit('getList')
    },
    // 
    onMutilDelete() {
      if (this.selected.length === 0) {
        error('')
        return
      }
      this.$emit('onMutilDel')
    },
    setDraggable() {
      const el = document.querySelectorAll(
        '.el-table__body-wrapper > table > tbody'
      )[0]
      this.draggableElement = Sortable.create(el, {
        onEnd: evt => {
          // 
          const targetRow = this.tableData.splice(evt.oldIndex, 1)[0]
          this.tableData.splice(evt.newIndex, 0, targetRow)
        }
      })
    },
    toggleRowSelection(...params) {
      this.$refs.dataTable.toggleRowSelection(...params)
    }
  }
}
</script>

