  @include flex-row;
  @include justify-between;
  @include flex-wrap;
  .left-block {
    @include flex-row;
    @include align-center;
    padding-bottom: 10px;

    .search-key,
    .search-input {
      width: 150px;
      margin-right: 5px;
    }
    .search-btn,
    .show-more-btn {
      margin: 0 5px 0 0;
    }
  }

  .right-block {
    @include flex-row;
    @include align-center;
    padding-bottom: 10px;
    .hidden-select,
    .hidden-reset,
    .add-btn {
      margin-right: 5px;
    }
    .hidden-select {
      max-width: 195px;
      min-width: 120px;
    }
  }
}
.common-table {
  .pagination-container {
    @include flex-row;
    @include align-center;
    margin-top: 30px;
  }
}
</style>

<style lang="scss">
@import '@/assets/css/flex.scss';
.common-table {
  .el-table {
    .operation {
      .el-button {
        margin: 5px 5px 5px 5px;
      }
    }
  }
  // icon
  .el-table__column-filter-trigger {
    .el-icon-arrow-down {
      font-size: 16px !important;
      color: black !important;
    }
  }
}
</style>
